import { Module } from '@nestjs/common';

import { ProjectScenario } from '../application/scenario/project';
import { ProjectRepository } from '../domain/repository';
import { ProjectTable } from '../infrastructure/persistence/project';
import {
  ProjectCreateController,
  ProjectUpdateController,
} from '../presentation/project/controller';

import { PrismaModule } from './prisma.module';
import { UtilityModule } from './utility.module';

@Module({
  imports: [UtilityModule, PrismaModule],
  providers: [
    ProjectScenario,
    {
      provide: ProjectRepository,
      useClass: ProjectTable,
    },
  ],
  controllers: [ProjectCreateController, ProjectUpdateController],
})
export class ProjectModule {}
